.compare-bikes {
  &__products {
    @include page-grid;

    row-gap: 40px;
    grid-column: 1 / -1;
  }

  &__product {
    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }

    @include hover(transform, scale(1.1));

    cursor: pointer;
  }
}
